}

.overlay-wrapper {
  position: relative;
  overflow: hidden;
}

.overlay {
  width: 100%;
  height: 70px;
  letter-spacing: -.04em;
  text-align: center;
  color: var(--main-text-clr);
  background: var(--accent-yellow-clr);
  border-radius: 0 0 8px 8px;
  margin: 0;
  padding: 30px 32px 20px;
  font-family: DM Sans;
  font-size: 14px;
  font-weight: 500;
  line-height: 18px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  bottom: 0;
  left: 0;
  overflow: auto;
  transform: translate(0, 100%);
}

.link:hover .overlay, .link:focus .overlay {
  transform: translate(0);
}

.seemorebtn {
  width: 124px;
  height: 46px;
  padding: 14px, 28px, 14px, 28px;
  border: 2px solid var(--accent-violet-clr);
  letter-spacing: -.01em;
  text-align: center;
  text-transform: uppercase;
  color: var(--main-text-clr);
  background: none;
  border-radius: 40px;
  margin-left: auto;
  margin-right: 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

.seemorebtn:hover, .seemorebtn:focus {
  background: var(--accent-violet-clr);
  color: #fff;
  border: none;
}

